<template>
  <!--<div id="map" class="map"></div>-->
  <div class="map-container">
    <l-map style="height: 500px; width: 900px" :zoom="zoom" :center="[38.717, -9.145]">
      <l-tile-layer :url="url"></l-tile-layer>
      <l-marker :lat-lng="[38.71, -9.14]" ><l-popup id="bold">Cervejaria Trindade</l-popup></l-marker>
      <l-marker :lat-lng="[38.717424, -9.134936]" ><l-popup id="bold">Clandestino</l-popup></l-marker>
      <l-marker :lat-lng="[38.718717, -9.164987]" ><l-popup id="bold">Moules & Beer</l-popup></l-marker>
      <l-marker :lat-lng="[38.709588, -9.135050]" ><l-popup id="bold">Tasca Kome</l-popup></l-marker>
      <l-marker :lat-lng="[38.723471, -9.154331]" ><l-popup id="bold">Forno d'Oro</l-popup></l-marker>
      <l-marker :lat-lng="[38.711644, -9.141586]" ><l-popup id="bold">Boa-Bao</l-popup></l-marker>
      <l-marker :lat-lng="[38.705853, -9.157711]" ><l-popup id="bold">Nogueiras</l-popup></l-marker>

    </l-map>
  </div>

</template>

<script>
import db from '../components/firebaseInit'
import {LMap, LTileLayer, LMarker, LPopup } from 'vue2-leaflet';

export default {
    name: 'LeafletMap',
    components: {
        LMap,
        LTileLayer,
        LMarker,
        LPopup
    },
    data: function() {
      return  {
            coords: null,
            map: null,
            tileLayer: null,
            url: 'https://cartodb-basemaps-{s}.global.ssl.fastly.net/rastertiles/voyager/{z}/{x}/{y}.png',
            zoom: 14,
            center: [38.723471, -9.154331],
            markerLatLng: [38.723471, -9.154331]
            }
          },

    mounted() {
      //this.initMap();
    },

    methods: {
      initMap() {
        this.map = L.map('map').setView([38.72, -9.145], 14);
        this.tileLayer = L.tileLayer(
          'https://cartodb-basemaps-{s}.global.ssl.fastly.net/rastertiles/voyager/{z}/{x}/{y}.png',
          {
            maxZoom: 18,
            attribution: '&copy; <a href="http://www.openstreetmap.org/copyright">OpenStreetMap</a>, &copy; <a href="https://carto.com/attribution">CARTO</a>',
          }
        );
        this.tileLayer.addTo(this.map);
        this.placeMarker();

      },
      placeMarker(){
        this.marker = L.marker([38.71, -9.14]).addTo(this.map).bindPopup("Cervejaria Trindade").addTo(this.map);
        this.marker = L.marker([38.717424, -9.134936]).addTo(this.map).bindPopup("Clandestino").addTo(this.map);
        this.marker = L.marker([38.718717, -9.164987]).addTo(this.map).bindPopup("Moules & Beer").addTo(this.map);
        this.marker = L.marker([38.709588, -9.135050]).addTo(this.map).bindPopup("Tasca Kome").addTo(this.map);
        this.marker = L.marker([38.723471, -9.154331]).addTo(this.map).bindPopup("Forno d'Oro").addTo(this.map);
        this.marker = L.marker([38.711644, -9.141586]).addTo(this.map).bindPopup("Boa-Bao").addTo(this.map);
        this.marker = L.marker([38.705853, -9.157711]).addTo(this.map).bindPopup("Nogueiras").addTo(this.map);
      },
    },
  }
</script>

<style lang="scss">
.map-container{
  display: flex;
  max-width: 95%;
}
#bold{
  font-weight: bold;
}
.map{
  height: 500px;
  width: 700px;
}
l-map {
  height: 500px;
  width: 700px;
}
</style>



